/* ---------------- Testimonial Carousel ---------------- */
const TestimonialCarousel: React.FC<{ isPaused: boolean }> = ({ isPaused }) => {
  const [currentIndex, setCurrentIndex] = useState(0);

  useEffect(() => {
    if (isPaused) return;
    const t = setInterval(
      () => setCurrentIndex((i) => (i + 1) % testimonials.length),
      4000
    );
    return () => clearInterval(t);
  }, [isPaused]);

  return (
    <div className="testimonial-carousel">
      <div className="testimonial-content">
        <div className="stars">
          {Array.from({ length: testimonials[currentIndex].stars }).map((_, i) => (
            <span key={i} className="star">
              ‚≠ê
            </span>
          ))}
        </div>
        <p className="testimonial-text">"{testimonials[currentIndex].text}"</p>
        <p className="testimonial-author">‚Äî {testimonials[currentIndex].author}</p>
      </div>

      <div className="carousel-dots">
        {testimonials.map((_, index) => (
          <button
            key={index}
            className={`dot ${index === currentIndex ? "active" : ""}`}
            aria-label={`Go to testimonial ${index + 1}`}
            onClick={() => setCurrentIndex(index)}
          />
        ))}
      </div>
    </div>
  );
};

/* ---------------- Contact Form ---------------- */
const ContactForm: React.FC<{ onFocus: () => void; onBlur: () => void }> = ({
  onFocus,
  onBlur,
}) => {
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  });

  const [okMsg, setOkMsg] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => setFormData({ ...formData, [e.target.name]: e.target.value });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    const ok = await submitGoogleForm({
      id: "YOUR_GOOGLE_FORM_ID", // <-- ADD YOUR GOOGLE FORM ID LATER
      ...formData,
      pagePath:
        typeof window !== "undefined"
          ? window.location.pathname
          : "/carpentry-in-04901",
      campaign: "central-maine-carpentry-hyperlocal",
    });

    if (ok) {
      setFormData({ name: "", phone: "", email: "", message: "" });
      setOkMsg(true);
      setTimeout(() => setOkMsg(false), 5000);
    } else {
      alert("Form not connected yet. Please add Google Form ID.");
    }
  };

  return (
    <div className="contact-form-container">
      <h3 className="form-title">Get a Free Estimate</h3>

      <form className="contact-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            name="name"
            type="text"
            placeholder="Full Name"
            value={formData.name}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required
          />
        </div>

        <div className="form-group">
          <input
            name="phone"
            type="tel"
            placeholder="Best Phone Number"
            value={formData.phone}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required
          />
        </div>

        <div className="form-group">
          <input
            name="email"
            type="email"
            placeholder="Your Email Address"
            value={formData.email}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
          />
        </div>

        <div className="form-group">
          <textarea
            name="message"
            placeholder="Tell us what you need (siding, windows, doors, decks, rot repair, roofing...)"
            rows={4}
            value={formData.message}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
          />
        </div>

        <button type="submit" className="submit-btn btn-primary">
          <span className="btn-icon">üìã</span>
          <span className="btn-text">Get My Free Estimate</span>
          <div className="btn-shine"></div>
        </button>
      </form>

      {okMsg && (
        <div className="hipo-success-message">
          <FaCheckCircle className="hipo-success-icon" />
          Thank you! We‚Äôll get back to you shortly.
        </div>
      )}
    </div>
  );
};

